navbar {
    position: fixed;
    min-width: 280px;
    margin-right: 60px;
    border-right: solid 3px #000;

    header {
        font-size: 30px;
        text-align: center;
        line-height: 2;
    }

    ul {
        margin: 0;
        padding: 0;
        list-style-type: none;
        overflow: auto;

        li {
            a {
                border-top: solid 1px;
                height: 40px;
                padding: 5px 0px 5px 26px;
                margin: 0px;
                line-height: 2;
                text-decoration: none;
                display: block;
        
                &, &:visited, &:active {
                    color: #000;
                }
            }
        }
    
    }
}

main {
    position: absolute;
    left: 300px;
}